:root {
  --color-primario: #E3DFFD;
  --color-h1-input-text-btn: #ECF2FF;
  --color-fondo-home: #BEADFA;
  --color-text-color-input: #002147;
  --color-negro-texto-inicio-border-select: #000000;
  --color-link-login-signup-btn: #869CFF;
  --color-blur-input: rgb(152, 186, 249);
  --color-placeholder: #908F8F;
  --color-span: #9874FF;
  -color-icono: #222222;
}
.createmeeting-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: var(--color-primario);
}
.createmeeting-container form button {
  margin-bottom: 2rem;
}
.createmeeting-container h1 {
  margin-top: 5rem;
  color: var(--color-negro-texto-inicio-border-select);
}
.createMeeting-small {
  width: 80%;
}
.createMeeting-span {
  border: thin solid var(--color-span);
  width: 1.5rem;
  height: 1.5rem;
  color: var(--color-span);
  border-radius: 50%;
  text-align: center;
  line-height: 20px;
}
.column-day {
  display: flex;
}
.column-schedule {
  display: flex;
  flex-direction: column;
}
.column-schedule p {
  width: auto;
}
.column-schedule div{
  width: 100%;
  display: flex;
}
.column-schedule div div {
  display: flex;
}
.column-schedule span {
  width: 100%;
  text-align: justify;
  text-indent: 15%;
  font-style: italic;
  color: var(--color-negro-texto-inicio-border-select);
}
.checkbox {
  width: 100%;
  display: inline-flex;
}
.checkbox input {
  width: 20%;
}
.checkbox label {
  color: var(--color-negro-texto-inicio-border-select);
  padding-left: 0.4rem;
  font-weight: 600;
}
.checkbox input:focus {
  box-shadow: none;
}
.column-schedule .div-schedule {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  padding: 0.5rem;
}
.day{
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}
.day hr {
  border: 1.6px solid var(--color-hr);
}
.duration-days input {
  padding: 1.1rem;
  width: 100%;
}
.duration-days input::placeholder, .duration-minutes select, .duration-days input {
  text-align: center;
}
.duration-minutes select {
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
}
.duration-minutes select:focus {
  outline: none;
   box-shadow: 0 -1px 5px var(--color-blur-input)inset, 0px -1px 5px var(--color-blur-input);
}
.duration .duration-days, .duration .duration-minutes {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.duration .duration-days label, .duration .duration-minutes label {
  width: 100%;
  text-align: center;
  margin-top: 0.5rem;
}
form .duration {
  width: 80%;
  display: flex;
  flex-direction: row;
  column-gap: 1rem;
}

.icons {
  width: 30%;
  display: flex;
  justify-content: space-between;
}
.icons ion-icon, .trash ion-icon {
  color: var(--color-titulos);
}
.label-container {
  width: 80%;
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}
.label-container label {
  width: 100%;
  text-align: left;
}
.schedule {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0.6rem 0;
}
.div-schedule p {
  margin: 0 0.5rem;
}
.div-schedule select {
  border: thin solid black;
  color: black;
  border-radius: 0.5rem;
  padding: 0.5rem;
}
.schedule select:focus {
  outline: none;
}
.schedules-container {
  width: 80%;
}
.schedules span {
  color: var(--color-blanco);
}
.selects {
  display: inline-flex;
  margin: 1rem 0;
}
.selects select {
  padding: 0.5rem;
  border-radius: 0.3rem;
  border: none;
}
.selects select:focus {
  outline: none;
  box-shadow: 0 -1px 5px var(--color-blur-input)inset, 0px -1px 5px var(--color-blur-input);
}
.selects span {
  margin-right: 1rem;
  margin-left: 1rem;
}
.column-schedule .trash {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

@media screen and (min-width: 750px) {
  .createmeeting-container {
    padding-left: 10rem;
    padding-right: 10rem;
  }
}
@media screen and (min-width: 950px) {
  .createmeeting-container form button {
    width: 50%;
  }
}